import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { GeocoderProvider } from '../../providers/geocoder/geocoder';
import { SettingsProvider } from '../../providers/settings/settings';
import { ProfileProvider } from '../../providers/profile/profile';
import { PopUpProvider } from '../../providers/pop-up/pop-up';
import { DirectorProvider } from '../../providers/director/director';

/**
 * Generated class for the HistoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-history',
  templateUrl: 'history.html',
})
export class HistoryPage {
  carts: any;
  public details: string = "menus";
  items: any[];
  foods: any[];
  myfood: string;
  drinks: any[];
  constructor(public gCode: GeocoderProvider, public director: DirectorProvider, public pop: PopUpProvider, public modalCtrl: ModalController, public settings: SettingsProvider, public navCtrl: NavController, public ph: ProfileProvider, public navParams: NavParams) {
  }

  ionViewDidLoad() {

    
  
    this.ph.getUserProfile().child('History').on('value', userProfileSnapshot => {
      this.items = [];
      let drinks = [];
      this.pop.SmartLoader('')
      userProfileSnapshot.forEach( snap => {
     
           
    
          
            this.items.push({
              key: snap.key,
              name: snap.val().Food,
              price: snap.val().Total,
              location: snap.val().Location,
              quantity: snap.val().FoodQuantity,
              drinks: snap.val().Drinks,
              date: snap.val().Date
            });

            drinks.push({
              drinks: snap.val().Drinks
            })

            drinks.forEach(element => {
              console.log(element.drinks)
              
            });

           
            this.carts = this.items
            console.log(this.items, this.drinks)
          
 

          return false
      
        });

      
    
    })


   

  }


  onClick(item){
    console.log(item)
    this.ph.getUserProfile().child('History').child(item.key).remove().then(()=>{
      
    })
  
  
}

Accept(){
  this.navCtrl.push('BasketPage')
}


}



